import React, { Component } from 'react'
import axios from "axios";

export default class Login extends Component {
    state={
        users:[],
        email:'',
        contraseña:'',
        condicion:''
    }
//Las funciones onChange obtendrán el valor ingresado por el usuario y  lo añadiran a las variables declaradas incialmente en state{}
    onChangeEmail = (e)=>{
        this.setState({
            email: e.target.value
        })
    }
    
    onChangeContraseña = (e)=>{
        this.setState({
            contraseña: e.target.value
        })
    }

    onSubmit = async (e)=>{//funcion para guardar un usuario en la base de datos
        e.preventDefault();//evita que refresque la pagina

        try {
           const res=await axios.get('http://localhost:8080/usuarios/'+this.state.email);
           this.setState({users:res.data});
           console.log(this.state.users);//imprime por consola el elemento buscado por el get
        } catch (error) {
            alert(error.response.data);
        }
        this.Validacion();
        if(this.state.condicion==true){//si los datos ingresados son correctos se iniciar sesion.
            console.log("iniciar sesion")
            this.iniciarSesion();
        }
        else   
      //  console.log("los datos no fueron validos")
        return
        this.setState({nombre:''});
        this.setState({email:''});
        this.setState({contraseña:''});
    }
    Validacion(){
    
        if(this.state.users!==null){
            console.log(this.state.users!==null);
        var emailData=this.state.users[0]["email"];//obtengo el email del usuario que está en la base de datos
        var contraseñaData=this.state.users[0]["contraseña"];//obtengo la contraseña del usuario que está en la base de datos
        if (emailData==this.state.email & contraseñaData===this.state.contraseña){//si el email y constraseña es correcto la funcion devuelve un true
             this.setState({condicion:true})
        }else 
             this.setState({condicion:false})
            
    }else alert("el correo ingresado es incorrecto");}
    iniciarSesion(){
        window.location.href ="http://localhost:3000/registros";
    }

    render() {
        return (
            
            <form onSubmit={this.onSubmit}>
                <h3>Iniciar sesión</h3>
                <div className="form-group">
                    <label>Email</label>
                    <input type="email" className="form-control" value={this.state.email} placeholder="ingrese email" onChange={this.onChangeEmail}/>
                </div>
                <div className="form-group">
                    <label>Contraseña</label>
                    <input type="password" className="form-control" value={this.state.contraseña} placeholder="ingrese Contraseña" onChange={this.onChangeContraseña}/>
                </div>
                <div className="form-group">
                    <div className="custom-control custom-checkbox">
                        <input type="checkbox" className="custom-control-input" id="customCheck1" />
                      </div>
                </div>

                <button type="submit" className="btn btn-dark btn-lg btn-block">Ingresar</button>
            </form>
            
        );
    }
}
