import React, { Component } from 'react'
import axios from "axios";

export default class SignUp extends Component {
    state = {
        users: [],
        usersE: [],
        nombre: '',
        email: '',
        contraseña: ''

    }

    async componentDidMount() {
        const res = await axios.get('http://localhost:8080/usuarios');
        this.setState({ users: res.data });
        console.log(this.state.users);//muestra el estado de los datos en la tabla usuario de la base de datos
    }
    //Las funciones onChange obtendrán el valor ingresado por el usuario y  lo añadiran a las variables declaradas incialmente en state{}
    onChangeNombre = (e) => {
        this.setState({
            nombre: e.target.value
        })
    }
    onChangeEmail = (e) => {
        this.setState({
            email: e.target.value
        })
    }
    onChangeContraseña = (e) => {
        this.setState({
            contraseña: e.target.value
        })
    }
    onSubmit = async (e) => {//funcion para guardar un usuario en la base de datos
        e.preventDefault();//evita que refresque la pagina
        const resE = await axios.get('http://localhost:8080/usuarios/' + this.state.email);
        this.setState({ usersE: resE.data });
        const resultado = this.state.users.find(e => e.nombre === this.state.nombre)

        console.log(resultado)
        console.log(this.state.users)
        if (resultado == undefined || resultado == null) {
            if (this.state.usersE !== null) {
                console.log("Entré1")
                try {
                    console.log("Entré2")
                    const res = await axios.post('http://localhost:8080/usuarios', {
                        nombre: this.state.nombre,//envios los datos requeridos al post del backend
                        email: this.state.email,
                        contraseña: this.state.contraseña
                    })
                    window.location.href = "http://localhost:3000/registros";

                    console.log(res);
                } catch (error) {
                    alert(error.response.data);
                    this.setState({ nombre: '' });
                    this.setState({ email: '' });
                    this.setState({ contraseña: '' });
                }
            } else
                alert("El correo ya existe o no se ha ingresado uno, ingrese uno nuevo");
        } else
            alert("Los nombre ya existe o no se ha ingresado");
    }

    render() {
        return (
            <form onSubmit={this.onSubmit}>
                <h3>Registrar</h3>
                <div className="form-group">
                    <label>Nombres</label>
                    <input type="text" className="form-control" value={this.state.nombre} placeholder="Nombres" onChange={this.onChangeNombre} />
                </div>

                <div className="form-group">
                    <label>Email</label>
                    <input type="email" className="form-control" value={this.state.email} placeholder="Ingrese email" onChange={this.onChangeEmail} />
                </div>

                <div className="form-group">
                    <label>Contraseña</label>
                    <input type="password" className="form-control" value={this.state.contraseña} placeholder="Ingrese contraseña" onChange={this.onChangeContraseña} />
                </div>

                <button type="submit" className="btn btn-dark btn-lg btn-block">Registrarse</button>
            </form>
        );
    }
}