import React from 'react';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import './App.css';//el archivo de diseño
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

import Login from "./Login";
import SignUp from "./Signup";
import logotipo from './img/logo.png'
//<Route path="/sign-in/registros" component={ShowRegistro}/>
function App() {
  return (<Router>
    <div className="App">

      <div className="outer">
        <div className="inner">
          <img src={logotipo} alt="" class='rounded mx-auto d-block' />
          <div className="form-group">
            <Link className="nav-link" to={"/sign-in"}>Iniciar Sesión</Link>
            <Link className="nav-link" to={"/sign-up"}>Registrarse</Link>
            <Switch>
              <Route path="/sign-in" component={Login} />
              <Route path="/sign-up" component={SignUp} />
            </Switch>
          </div>
        </div>
      </div>
    </div></Router>
  );
}

export default App;
