import React, { Component } from 'react'
import '../perfil.css';
import axios from "axios";

export default class ShowRegistro extends Component {
    state = {
        users: [],
        idRegistro: '',
        temperatura: '',
        fecha: '',
        idPresona: '',
        idNegocio: '',
        valorFiltro: ''

    }
    onSubmit = async (e) => {//muestra la lista de los estudiantes actualizada
        e.preventDefault();//evita que refresque la pagina
        const res = await axios.get('http://localhost:8080/registros');
        this.setState({ users: res.data });
        console.log(this.state.users);
    }
    onSubmitFiltrar = async (e) => {
        e.preventDefault();
        var RE = /^\d*(\.\d{1})?\d{0,1}$/;//cod para valida un numero decimal
            if (RE.test(this.state.valorFiltro)) {//valida si el numero ingresado es decimal
                console.log(true)
                var num=parseFloat(this.state.valorFiltro);
                console.log("Numero: "+num)
                const res = await axios.get('http://localhost:8080/registros/'+num);
                this.setState({ users: res.data });
                console.log(this.state.users);
                return true;
            } else {
                console.log(false)
                return false;
            }
        
        
    }
    onChangeFiltrar = (e) => {
        this.setState({
            valorFiltro: e.target.value
        })
    }

    render() {
        return (
            <div className="row">
                <div className="col-md-4">
                    <div className="card card-body">
                        <h1>Bienvenidos a la base de registros</h1>
                        <form onSubmit={this.onSubmit}>
                            <button className="btn btn-primary" type='submit'>Consultar</button>
                        </form>
                        <em>Filtrar los que sean de mayor o igual a: </em>
                            <form onSubmit={this.onSubmitFiltrar}>
                                <input type="text" className="form-control" value={this.state.valorFiltro} placeholder="Ingrese Temperatura" onChange={this.onChangeFiltrar} />
                                <button className="btn btn-primary" type='submit'>Filtrar</button>
                            </form>
                            <form onSubmit={this.onSubmitCerrar}>
                            <li><a href="http://localhost:3000" onclick="return confirm('Are you sure to logout?');">Cerrar sesion</a></li>

                               
                            </form>

                    </div>
                </div>
                <div className="col-md-8">
                    <table className="table">
                        <thead className="thead-dark">
                            <tr>
                                <th scope="col">#</th>
                                <th scope="col">Temp °C</th>
                                <th scope="col">Fecha</th>
                                <th scope="col">CI</th>
                                <th scope="col">Negocio</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                this.state.users.map(user => (
                                    <tr key={user.id_registro} id={user.id_registro}>
                                        <th scope="row" bgcolor="silver" >{user.id_registro} </th>
                                        <td bgcolor="White">{user.temperatura}</td>
                                        <td bgcolor="White">{user.fecha}</td>
                                        <td bgcolor="White">{user.id_persona}</td>
                                        <td bgcolor="White">{user.id_negocio}</td>
                                    </tr>)
                                )
                            }
                        </tbody>
                    </table>
                </div>
            </div>

        )
    }
}